<template>
  <div id="app">
    <div class="btnWrapper">
      <div class="inputWrapper">
        <div class="text">今个月电费：</div>
        <input class="lastMonth" type="text" v-model="thisSlide" placeholder="今个月电费">
      </div>
      <div class="inputWrapper">
        <div class="text">上个月电费：</div>
        <input class="thisMonth" type="text" v-model="lastSlide" placeholder="上个月电费">
      </div>
    </div>
    <div class="btnWrapper">
      <div class="inputWrapper">
        <div class="text">今个月水费：</div>
        <input class="lastMonth" type="text" v-model="thisWater" placeholder="今个月水费">
      </div>
      <div class="inputWrapper">
        <div class="text">上个月水费：</div>
        <input class="thisMonth" type="text" v-model="lastWater" placeholder="上个月水费">
      </div>
    </div>  
    
    <div class="price">
      <div class="text">房租:</div>
      <input type="text" v-model="thisHome" placeholder="房租">
    </div>
    <div class="totalprice">总价：{{totalPrice}}元</div>
    <div class="reset" @click="reset">
      重置
    </div>
  </div>
  
</template>

<script>

export default {
  data(){
    return {
      lastWater:'',
      thisWater:'',
      lastSlide:'',
      thisSlide:'',
      thisHome:''  
    }
  },
  computed:{
    totalPrice(){
      let waterPrice = (this.thisWater - this.lastWater + 2) * 4;
      let slidePrice = (this.thisSlide - this.lastSlide + 2) * 1.5;
      return parseInt(this.thisHome) + waterPrice + slidePrice
      
    }
  },
  methods:{
    reset(){
      this.lastWater = '';
      this.thisWater = '';
      this.lastSlide = '';
      this.thisSlide = '';
      this.thisHome = ''
    }
  }
}
</script>

<style lang="less">
#app {
  width:100%;
  box-sizing:border-box;
  padding:10px;
  overflow: hidden;
}
.btnWrapper {
  margin-bottom:10px;
  width:100%;
  overflow: hidden;
}
.inputWrapper {
  width:100%;
  box-sizing:border-box;  
  border:1px solid #ccc;
  margin-bottom:15px;
  .text {
    width:100%;
    text-align: center;
    line-height: 30px;
    border-bottom:1px solid #ccc;
    background: #eee;
    font-size:24px; 
  }
  input {
    width:100%;
    text-align: center;
    line-height: 50px;
    border:none;
    font-size:26px;
  }
}
.price {
  width:100%;
  overflow: hidden;
  margin-bottom:10px; 
   box-sizing:border-box;  
  border:1px solid #ccc;
  .text {
    width:100%;
    text-align: center;
    line-height: 30px;
    border-bottom:1px solid #ccc;
    background: #eee;
    font-size:24px; 
  }
  input {
    width:100%;
    text-align: center;
    line-height: 50px;
    border:none;
    font-size:26px;
  }
}
.totalprice {
  width:100%;
  height:100px;
  background:#f40;
  color:#fff;
  text-align: center;
  line-height: 100px;
  overflow: hidden;
  font-size:30px;
  margin-bottom:10px;
}
.reset {
  width:100%;
  height:50px;
  background:#ccc;
  color:#000;
  font-size:18px;
  line-height:50px;
  text-align: center;
}
</style>



